import React, {useEffect,useState} from 'react'
import styled from 'styled-components'
import RightSide from '../../component/RightSide/RightSide'
import { Auth } from 'aws-amplify';
import history from '../../utils/history'
import Header from '../../component/Header'
import Footer from '../../component/Footer'
import { callPlace } from '../../Api'
import ValueLoader from '../../utils/loader'
const DashboardContainer = styled.div`
display:flex;
background: linear-gradient(157.1deg, #FF7171 -1.1%, #FF479D 100%);
@media (max-width:767px){
    flex-direction: column;  
}
`
const Container = styled.div`
max-width:1440px;
width:100%;
padding:0 30px;
margin:35px auto 0;
@media{
  padding:0 15px;
   margin:15px auto 0;
}
`


const Dashboard = () => {
  const [placeValue,setPlace]=useState()
    useEffect(() => {
        let updateUser = async authState => {
          try {
            const value = await Auth.currentAuthenticatedUser()
            const place = await callPlace(value.attributes.sub)
                setPlace(place[0])
        } catch {
            history.push('/business/login')
            window.location.reload() 
          }
        }
        updateUser()
      // eslint-disable-next-line react-hooks/exhaustive-deps
      }, []);
    
    return(
    <>
    {placeValue?
       <DashboardContainer>
         <Container>
          <Header value={placeValue} />
          <RightSide/>
          <Footer />
       </Container>
       </DashboardContainer>:<div style={{textAlign:'center' ,margin:' 40px auto 0'}}><ValueLoader height="100" width="100" /></div>
   }</>)
}

export default Dashboard